.main {
    display: flex;
    flex-direction: column;

    &__inputs {
        display: flex;
        gap: 10px;
        justify-content: space-between;

        input {
            width: 100%;
            border:1px solid lightblue;
        }

        button {
            padding: 10px;
            background: lightblue;
        }
    }

    &__block {
        display: flex;
        align-items: flex-start;
        gap: 20px;

        img {
            border: 1px solid black;
            width: 100%;
            max-width: 128px;
        }

        
    }

    &__info {
        display: flex;
        flex-direction: column;
        gap: 10px;

    }

    &__help {
        display: flex;
        flex-direction: column;
        gap: 10px;
    }

    &__help-item {
        display: block;
        cursor: pointer;
        // cursor: pointer;
    }
}